@font-face {
  font-family: "Clear Sans";
  src: url("../src/clear-sans.ttf") format("truetype");
}

body {
  display: flex;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(87, 64, 124, 1) 47%,
    rgba(0, 212, 255, 1) 100%
  );
  color: white;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  font-family: "Clear Sans", sans-serif;
  font-size: 21px;
}

.text {
  order: 2;
  padding-top: 40px;
  width: 440px;
  font-weight: bold;
}

.game {
  user-select: none;
  cursor: default;
}

.details-box {
  display: flex;
  align-items: center;
  padding: 10px;
  justify-content: space-between;
}

.questionRow {
  margin: 10px 0;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.question-text {
  margin: 5px;
}

.score-header {
  font-size: 50%;
}

.score-box {
  background-color: #3d2963;
  color: rgba(255, 255, 255, 0.5);
  min-width: 50px;
  width: 70px;
  padding: 5px 15px;
  border-radius: 7px;
}

.scopes-row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.scope-image {
  width: 240px;
  height: 200px;
  margin: 20px;
}

.scope-description {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

.scope-selected {
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.5);
}

.scope-not-selected {
  box-shadow: 0 0 12px rgba(255, 255, 255, 0);
}

.effort-slider {
  width: 100%;
}

.effort-label {
  font-weight: bold;
  text-align: center;
}

.effort-label-low {
  font-size: 20px;
}

.effort-label-med {
  font-size: 30px;
}

.effort-label-high {
  font-size: 48px;
}

.action-text {
  font-size: 32px;
  font-weight: bold;
}

.act-button {
  background-color: #3d2963;
  color: rgba(255, 255, 255, 0.9);
  cursor: pointer;
  font-size: 25px;
  padding: 10px 20px;
  border-radius: 7px;
  transition: color 0.3s ease, background-color 0.3s ease;
  &:hover {
    background-color: #6c49ac;
    color: rgba(255, 255, 255, 1);
  }
}

